// app/preview/[productId]/layout.tsx
import type { Metadata } from "next";
import axiosClient from "@/lib/axiosClient";

type Props = {
  params: { slug: string };
};

export async function generateMetadata(): Promise<Metadata> {
  const productId = params.slug;
 console.log("Generating metadata for productId:", productId);
  try {
    const res = await axiosClient.get(`/products/${productId}/landing-page`);

    const seo = res.data?.data;
    return {    
      title: seo?.meta_title ?? "Landing Page Preview",
      description: seo?.meta_description ?? "Preview landing page",
      keywords: seo?.keywords ?? [],

      openGraph: {
        title: seo?.meta_title,
        description: seo?.meta_description,
        locale: seo?.locale ?? "en_IN",
      },

      robots: {
        index: false,
        follow: false,
      },
    };
  } catch {
    return {
      title: "Landing Page Preview",
      robots: { index: false, follow: false },
    };
  }
}

export default function Layout({
  children,
}: {
  children: React.ReactNode;
}) {
  return <>{children}</>;
}
